import { useRecoilValue } from 'recoil';
import { totalPriceSelector } from '../../../../recoil/cartState';
import * as Styled from './TotalPrice.styled';

const TotalPrice = () => {
  const totalProductPrice = useRecoilValue(totalPriceSelector);
  const deliveryFee = totalProductPrice ? 3000 : 0;

  return (
    <Styled.PriceSection>
      <Styled.TitleParagraph></Styled.TitleParagraph>
      <Styled.PriceDiv>
        <Styled.PriceParagraph> </Styled.PriceParagraph>
        <Styled.PriceParagraph>{totalProductPrice.toLocaleString('ko-KR')}</Styled.PriceParagraph>
      </Styled.PriceDiv>
      <Styled.PriceDiv>
        <Styled.PriceParagraph> </Styled.PriceParagraph>
        <Styled.PriceParagraph>{deliveryFee.toLocaleString('ko-KR')}</Styled.PriceParagraph>
      </Styled.PriceDiv>
      <Styled.PriceDiv>
        <Styled.PriceParagraph> </Styled.PriceParagraph>
        <Styled.PriceParagraph>
          {(totalProductPrice + deliveryFee).toLocaleString('ko-KR')}
        </Styled.PriceParagraph>
      </Styled.PriceDiv>
      <Styled.OrderButton type="button"></Styled.OrderButton>
    </Styled.PriceSection>
  );
};

export default TotalPrice;
